<template>
  <div class="container">
    <div class="card center">
      <h1>План по изучению Vue.js</h1>
      <p class="description">{{ title || this.buttons[0].text }}</p>
      <nav>
        <ul class="list">
          <li
              class="list-item"
              v-for="button in buttons"
              :key="button.id"
          >
            <button
                type="button"
                :class="{
                'btn': true,
                'primary': button.id <= activeButton,
                'active': button.id === activeButton
                }"
                @click="navButtonHandler(button.id)"
            >
              {{ button.id }}
            </button>
            {{ button.title }}
          </li>
        </ul>
        <div v-if="!isFinished">
          <button
              :class="{
            'btn': true,
            'disabled': activeButton === 1,
            }"
              v-on:click="prevPage"
          >
            Назад
          </button>
          <button
              v-if="activeButton !== buttons.length"
              class="btn primary"
              @click="nextPage"
          >
            Вперед
          </button>
          <button
              v-else
              class="btn primary"
              @click="isFinished = true"
          >
            Завершить
          </button>
        </div>
        <button
            v-else
            class="btn"
            @click="startOver"
        >
          Начать заново
        </button>

      </nav>
    </div>
  </div>
</template>

<script>
export default {
  name: 'App',
  components: {},
  data() {
    return {
      buttons: [
        {
          id: 1,
          title: 'основы',
          text: 'В блоке вы познакомитесь со всеми основами Vue.js на практике. На протяжении блока мы напишем реактивное приложение, в процессе разработки которого разберем всю базу фреймворка.',
        },
        {
          id: 2,
          title: 'компоненты',
          text: 'Один из самых важных блоков в курсе, где вы узнаете все о компонентах. В блоке мы напишем 2 разных приложения и создадим более5 различных UI компонентов как в реальной разработке. Блок расскажетпро абсолютно все составляющие, которые есть в компонентах:взаимодействие, slots, асинхронные и диинамические компоненты и тоннапримеров.'
        },
        {
          id: 3,
          title: 'роутер',
          text: 'В данном блоке вы узнаете все о том, как работает мультиязычность во Vue. Мы создадим миниклон Gmail в данном блоке, где вы на практике увидите как работать с динамическим роутером.',
        },
        {
          id: 4,
          title: 'vuex',
          text: 'В блоке вы узнаете абсолюно все про Vuex. Вы узнаете как работать с данными, какие есть лучшие практики по их программировнию и структурированию. Все на практике.',
        },
        {
          id: 5,
          title: 'composition',
          text: 'Одним из наиболее важных обновлений Vue 3 является появление альтернативного синтаксиса Composition Api. В этом блоке вы узнаете абсолютно все, чтобы полностью пользоваться данным синтаксисом на практических примерах. Помимо этого вы узнаете как работать совместно с Vue Router и Vuex.'
        },
      ],
      activeButton: 1,
      title: '',
      isFinished: false,
    };
  },
  methods: {
    nextPage() {
      const {
        activeButton,
        buttons,
        changeDescription
      } = this;

      if (activeButton !== buttons.length) {
        this.activeButton++;
        changeDescription();
      }
    },
    prevPage() {
      if (this.activeButton !== 1) {
        this.activeButton -= 1;
        this.changeDescription();
      }
    },
    navButtonHandler(buttonId) {
      this.activeButton = buttonId;
      this.changeDescription();
    },
    changeDescription() {
      this.title = this.buttons[this.activeButton - 1].text;
    },
    startOver() {
      this.activeButton = 1;
      this.isFinished = false;
    }
  }
};
</script>

